}
.overlayer{
  background-image: #1e1e1e;
  background-image: -webkit-linear-gradient(top, transparent, #1e1e1e); /* For Chrome and Safari */
  background-image:    -moz-linear-gradient(top, transparent, #1e1e1e); /* For old Fx (3.6 to 15) */
  background-image:     -ms-linear-gradient(top, transparent, #1e1e1e); /* For pre-releases of IE 10*/
  background-image:      -o-linear-gradient(top, transparent, #1e1e1e); /* For old Opera (11.1 to 12.0) */
  background-image:         linear-gradient(top, transparent, #1e1e1e); /* Standard syntax; must be last */
}
.overlayer-h{
  background-image: rgba(0,0,0,0.6);
  background-image: -webkit-linear-gradient(right, transparent, rgba(0,0,0,0.6)); /* For Chrome and Safari */
  background-image:    -moz-linear-gradient(right, transparent, rgba(0,0,0,0.6)); /* For old Fx (3.6 to 15) */
  background-image:     -ms-linear-gradient(right, transparent, rgba(0,0,0,0.6)); /* For pre-releases of IE 10*/
  background-image:      -o-linear-gradient(right, transparent, rgba(0,0,0,0.6)); /* For old Opera (11.1 to 12.0) */
  background-image:         linear-gradient(right, transparent, rgba(0,0,0,0.6)); /* Standard syntax; must be last */
}

