/*common*/
.platform-android a:focus{
  border: 0;
  outline: none;
}

/*tabs*/
.platform-android .tabs-striped .tab-item{
  opacity: 1;
}
.platform-android .tabs-striped.tabs-positive .tab-item.tab-item-active{
  color: inherit;
  border-color: transparent;
}
.platform-android .tabs-striped .tab-item + .tab-item:before{
  display: none;
}

/*hot*/
.platform-android .hotop img{
  top: 18px;
}

/*me*/
.platform-android .myevent_list .item-avatar,
.platform-android .myevent_list .item-avatar.over{
  padding-left: 22px;
  background-position: 16px center;
  background-size: 24%;
}
.platform-android .line_vertical{
  left: 59px;
}
.platform-android .myevent_list .item-avatar .title{
  width: 174px;
}
.platform-android .about_me .myinfo .text-center.mt5{
  margin-left: 6px;
  text-align: left;
}
.platform-android ion-infinite-scroll .scroll-infinite{
  margin-top: -90px;
}
.platform-android .about_me .right-buttons .round-dot-red{
  top: 6px;
}
/*profile*/
.platform-android .profile .item input{
  padding-right: 16px;
}
/*publish*/
.platform-android .publish .input-image{
  height: 72px;
  width: 108px;
}
.platform-android .o-item-tabs input{
  width: 310px;
}
.platform-android .o-item-tabs .col-20{
  width: 82px;
}
.platform-android .o-item-tabs .format .col-33{
  width: 110px;
}
.platform-android .o-item-tabs .format .col-20{
  float: none;
  display: inline-block;
  /*color: #000;*/
  width: 96px;
  padding-left: 5px;
}
